import React, { Component } from "react";
import AppBar from "@material-ui/core/AppBar";
import ToolBar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";

import { scroller } from "react-scroll";
import MoreIcon from "@material-ui/icons/MoreVert";

class Header extends Component {
  state = {
    drawer: false,
    headerShow: true,
  };

  componentDidMount() {
    window.addEventListener("scroll", this.handelScroll);
  }
  componentWillUnmount() {
    window.removeEventListener("scroll", this.handelScroll);
  }

  handelScroll = () => {
    if (window.scrollY > 0) {
      this.setState({
        headerShow: false,
      });
    } else {
      this.setState({
        headerShow: true,
      });
    }
  };
  scrollToElement = (element) => {
    scroller.scrollTo(element, {
      duration: 1500,
      delay: 100,
      smooth: true,
    });
  };

  MenuIconHandeler = () => {
    this.setState({ drawer: !this.state.drawer ? true : false });
  };

  render() {
    return (
      <div id="header">
        {/* <SideDrawer open={this.state.drawer} close={this.MenuIconHandeler} /> */}
        <AppBar
          style={{
            background: this.state.headerShow ? "transparent" : "#fff",
            boxShadow: "none",
          }}
          position="fixed"
        >
          <Toolbar
            style={{
              display: "flex",
              flexDirection: "row",
              justifyContent: "space-between",
            }}
          >
            <img
              src={require("../../../image/100ppi/logo.png")}
              style={{ width: 200 }}
              alt="logo"
            />
            <div
              style={{
                width: "40%",
                display: "flex",
                justifyContent: "space-around",
              }}
            >
              <Button>About us</Button>
              <Button>Products</Button>
              <Button>Contact us</Button>
              <Button>Partner with us</Button>
            </div>
            {/* <div>
              <IconButton
                edge="end"
                aria-label="account of current user"
                aria-controls={menuId}
                aria-haspopup="true"
                onClick={handleProfileMenuOpen}
                color="inherit"
              >
                <SearchIcon style={{ color: "#767272" }} />
              </IconButton>
              <span
                style={{ color: "#767272", marginLeft: 10, marginRight: -10 }}
              >
                EN
              </span>
              <IconButton
                aria-label="account of current user"
                aria-controls={menuId}
                aria-haspopup="true"
                onClick={handleProfileMenuOpen}
                color="inherit"
              >
                <ArrowDropDownIcon style={{ color: "#767272" }} />
              </IconButton>
            </div> */}
            <div>
              <IconButton
                aria-label="show more"
                // aria-controls={mobileMenuId}
                aria-haspopup="true"
                // onClick={handleMobileMenuOpen}
                color="inherit"
              >
                <MoreIcon />
              </IconButton>
            </div>
          </Toolbar>
        </AppBar>
      </div>
    );
  }
}

export default Header;
