import React, { Component } from "react";
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
const responsive = {
  superLargeDesktop: {
    // the naming can be any, depends on you.
    breakpoint: { max: 4000, min: 3000 },
    items: 5,
  },
  desktop: {
    breakpoint: { max: 3000, min: 1024 },
    items: 4,
  },
  tablet: {
    breakpoint: { max: 1024, min: 464 },
    items: 2,
  },
  mobile: {
    breakpoint: { max: 464, min: 0 },
    items: 1,
  },
};

class index extends Component {
  render() {
    return (
      <div id="in_the_news">
        <h1 className="header">
          IN THE <span className="primary_color">NEWS</span>
        </h1>
        <Carousel responsive={responsive}>
          <div className="in_the_news_item">
            <h1>14</h1>
            <h2>May</h2>
            <p>Export,Real Estate and IT Enabled Services.</p>
            <img src={require("../../../image/100ppi/pawn.png")} />
          </div>
          <div className="in_the_news_item in_the_news_item_active">
            <h1 className="primary_color">11</h1>
            <h2 className="primary_color">Jun</h2>
            <p>Export,Real Estate and IT Enabled Services.</p>
            <img src={require("../../../image/100ppi/pawn.png")} />
          </div>
          <div className="in_the_news_item ">
            <h1>19</h1>
            <h2>Jul</h2>
            <p>Export,Real Estate and IT Enabled Services.</p>
            <img src={require("../../../image/100ppi/pawn.png")} />
          </div>
          <div className="in_the_news_item">
            <h1>18</h1>
            <h2>Jun</h2>
            <p>Export,Real Estate and IT Enabled Services.</p>
            <img src={require("../../../image/100ppi/pawn.png")} />
          </div>
          <div className="in_the_news_item">
            <h1>18</h1>
            <h2>Jun</h2>
            <p>Export,Real Estate and IT Enabled Services.</p>
            <img src={require("../../../image/100ppi/pawn.png")} />
          </div>
          <div className="in_the_news_item">
            <h1>18</h1>
            <h2>Jun</h2>
            <p>Export,Real Estate and IT Enabled Services.</p>
            <img src={require("../../../image/100ppi/pawn.png")} />
          </div>
        </Carousel>
        ;
      </div>
    );
  }
}

export default index;
